.banner {
  margin-top: -10vh;
  height: 80vh;
  margin-bottom: -80px;
  color: white;
  object-fit: contain;
}
.banner_contents {
  margin-left: 30px;
  padding-top: 300px;
  /* height: 190px; */
}

.banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}
.banner_description {
  width: 45rem;
  line-height: 1;
  padding: 1rem 0;
  font-size: 1rem;
